<template>
    <div class="content"> 
        <h1>Luyện tập chương {{chapterNumber}}</h1>
        <div v-if="isStarted">
            <vue-countdown-timer
                @end_callback="endCallBack()"
                :start-time="startTime"
                :end-time="startTime + time * 1000"
                :interval="1000"
                :start-label="'Until start:'"
                :end-label="'Thời gian còn lại'"
                label-position="begin"
                :end-text="'Hết giờ!'"
                :day-txt="'ngày'"
                :hour-txt="'giờ'"
                :minutes-txt="'phút'"
                :seconds-txt="'giây'">
            </vue-countdown-timer>
            <Test/>
        </div>
        <div v-else> 
            <h2>Số câu hỏi: {{nQuestion}}</h2>
            <h2>Thời gian: {{convert(time)}}</h2>
            <b-button @click="onClick()" class="is-info">Bắt đầu</b-button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import Test from './Test.vue'

const API_URL = "http://20.212.191.77:8000/api/"

export default{
    name: 'TestInfo',
    components: {
        Test,
    },
    data(){
        return {
            isStarted: false,
			nQuestion: 0,
            time: null, 
            startTime: 0
        }
    },
    methods: {
        convert: function(seconds) {
            return new Date(seconds * 1000).toISOString().substr(11, 8);
        },
        onClick: function(){
            this.isStarted = true;
            this.startTime = new Date().getTime();
        },
        endCallBack: function(){
            console.log('end')
        }
    },
    computed: {
        chapterNumber: function(){
            return Number(this.$route.params.test_id) + 14;
        }
    },
    beforeMount: function() {
		const testID = this.$route.params.test_id;
		axios.get(API_URL + 'test_info?test_id=' + testID)
			.then((response) => {
				this.time = response.data.time;
                this.nQuestion = response.data.n_ques
            })
	}
}
</script>
