<template>
	<div>
		<div v-if="isLoading">
			<img src="@/assets/Ajux_loader.gif" style="width: 200px; height: 200px">
		</div>
		<div v-else>
			<b-steps
				:has-navigation="true"
				v-model="activeStep"
				animated>
				<template v-for="(question, index) in questions">
					<b-step-item
						:step="index + 1"
						:key="index">
						<div v-html="question.fields.question">
						</div>
						<div style="text-align: left;">
							<section>
								<div class="answer-container">
									<b-field v-for="(option, opindex) in options[index]" :key="opindex">
										<b-radio
											:disabled="showResult[index]"
											v-model="ans[index]"
											:native-value="option.fields.option">
											<div v-html="option.fields.option"></div>
										</b-radio>
									</b-field>
								</div>
							</section>
						</div>
						<b-button @click="check(index)"
							class="is-info"
							:disabled="showResult[index]">
							Kiểm tra đáp án
						</b-button>
						<div>
							<div v-if="showResult[index]"> 
								<hr class="solid"/>
								<div v-if="isChoice[index]">  
									<div v-if="ans[index] == question.fields.right_answer">
										<b-icon
											type="is-success"
											icon="check-circle"
											size="is-medium">
										</b-icon>
										<span style="font-size: 34px; margin-left: 10px; color: #48c78e">Chính xác</span>
									</div>
									<div v-else> 
										<b-icon
											type="is-danger"
											icon="close-circle"
											size="is-medium">
										</b-icon>
										<span style="font-size: 34px; margin-left: 10px; color: #f14668">Chưa chính xác</span>
									</div>
								</div>
								<div v-html="question.fields.explaination"> 
								</div>
							</div>
						</div>
					</b-step-item>
				</template>
			</b-steps>
		</div>
	</div>
</template>

<script>
import axios from 'axios';

const API_URL = "http://20.212.191.77:8000/api/"

export default {
  name: 'Test',
  data() {
		return {
			activeStep: 0,
			isLoading: true,
			questions: [],
			options: [], 
			ans: [],
			showResult: [],
			isChoice: [],
			answered: 0,
			total: 0,
			right: 0
		}
	},
	methods: {
		showOverallResult: function() {
			for (let i = 0; i < this.questions.length; i++){
				if (this.isChoice[i]){
					this.total++;
					if (this.ans[i] == this.questions[i].fields.right_answer)
						this.right++;
				}
			}

			this.$buefy.dialog.alert({
				title: 'Bạn đã hoàn thành bài thi',
				message: 'Kết quả: ' + this.right + '/' + this.total,
				confirmText: 'Trở về trang chủ',
				onConfirm: () => {this.$router.push('/')}
			})
		},
		check: function(index) {
			this.showResult.splice(index, 1, true)
			this.answered++;
			if (this.answered == this.questions.length)
				this.showOverallResult();
		}
	},
	beforeMount: function() {
		const testID = this.$route.params.test_id;
		axios.get(API_URL + 'question?test_id=' + testID)
			.then(async (response) => {
				this.questions = response.data.questions; 
				const promises = this.questions.map(async question => {
					return axios.get(API_URL + 'option?id=' + question.pk)
				})
				const ques = await Promise.all(promises)
				console.log(ques)
				for (let i = 0; i < this.questions.length; i++){
					this.ans.push('')
					this.showResult.push(false);
					this.isChoice.push(this.questions[i].fields.is_choice);
					this.options.push(ques[i].data.options);
				}
				this.isLoading = false;
			});

	}
}
</script>

<style scoped>
.button {
    margin-top: 20px;
}

.answer-container {
	width: 500px; text-align: left; margin: auto
}

hr.solid {
  border-top: 1px solid #bbb;
}
</style>