import Vue from 'vue'
import Router from 'vue-router'
import TestInfo from '../components/TestInfo';
import MainPage from '../components/MainPage';

Vue.use(Router)

var router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/', 
            name: 'MainPage', 
            component: MainPage 
        },
        {
            path: '/test/:test_id', 
            name: 'TestInfo', 
            component: TestInfo 
        }
        // {
        //     path: '/download', 
        //     name: 'Download',
        //     component: Download,
        //     meta: {
        //         title: 'Download'
        //     },
        // }
    ]
})

export default router